import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button , Form } from 'react-bootstrap';
import {Link, withRouter} from 'react-router-dom'
import { toast } from 'react-toastify'
import services from '../services';
// import 'react-toastify/dist/ReactToastify.css';
// import backgroundImage from './logHour.jpg'
// import Login from './Login/login'

class Login extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      email : '',
      password : '',
    }
  }
  
  handleFields = (e) => {
    if(e.target.name == 'email'){
      this.setState({
        email : e.target.value
      })
    }
    if(e.target.name == 'password'){
      this.setState({
        password : e.target.value
      })
    }
  }

  handleLogin = async () => {
    let reg = /^([A-Za-z0-9_\-\.])+\@enukesoftware.com$/;

    if(this.state.email === '' || this.state.password === ''){
        toast.error("Email and Password Required");
    }else if (this.state.email !== '' && !reg.test(this.state.email)) {
        toast.error("Enter Correct Email")
    }else{
        let _r = await services.auth.login(this.state.email,this.state.password);
        if(_r.success){
          window.localStorage.setItem('username',_r.data.name);
          window.localStorage.setItem('user_id',_r.data.id)
          window.localStorage.setItem('token',_r.data.token);
          window.localStorage.setItem('userType',_r.data.role_id)
          console.log(".........",_r.data)
          toast.success('Successfully Login')
          window.location = '/'
        }else{
          toast.error(_r.error)
        }
    }
  }

  render(){
    return (
      <div>
        <Form className="font-all"> 
        <Form.Group controlId="formBasicEmail">
          <Form.Label style={{display:"flex"}}>Email</Form.Label>
          <div className="input-div">
            <img className="input-img" src={require('../img/Email.png')} alt='email' />
            <Form.Control type="email" name='email' className="auth-fields" value={this.state.email} onChange={this.handleFields} placeholder="Enter Email" />
          </div>
        </Form.Group>
        <Form.Group>
          <Form.Label style={{display:"flex"}}>Password</Form.Label>
          <div className="input-div">
            <img className="input-img" src={require('../img/Password.png')} alt='password' />
            <Form.Control type="password" required name='password' className="auth-fields" value={this.state.password} onChange={this.handleFields} placeholder="Password" />
          </div>
        </Form.Group>
        <div className="font-label text-center">
          <Link to={'/signup'} className="float-left">SignUp</Link>
          <label className="ml-5">|</label>
          <Link className="float-right" to={'/forgot_password'}>ForgotPassword?</Link>
        </div>
        <Button className="auth-button mt-1" onClick={this.handleLogin} type="button">
          Login
        </Button>
      </Form>
      </div>
    )
  }
}

export default withRouter(Login);
