import React from 'react';
// import Routes from './Routes'
import AppSwitch from './Routes/AppSwitch';
import AuthSwitch from './Routes/AuthSwitch';

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      isLogged : window.localStorage.getItem('token') ? true : false
    }
  }

  componentDidMount() {
      
  }

  render(){
    console.log("App",this.state,this.props)
    return (
      <>
        <div style={{backgroundImage:`url(${require('./img/clock.png')})`}}>
          {
            this.state.isLogged ? <AppSwitch /> : <AuthSwitch/>
          }
        </div>
      </>
    )
  }
}

export default App;
