import React from 'react';
import { Table, Button, OverlayTrigger,Tooltip, Form , Modal} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import services from '../../../services';
import ReactPaginate from 'react-paginate';
import { toast } from 'react-toastify';
import ToolTipComponent from '../../common/Toottip';

const column = [
    {
        name:"Name",
        key:"name"
    },
    {
        name:"Pending Task (This Week)",
        key:"pending_task"
    },
    {
        name:"Email",
        key:"email"
    },
    {
        name:"Contact",
        key:"contact"
    }
]

function AddEmployeeModal(props) {
    return (
      <Modal
        {...props}
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
            <div className="text-center font-all">
                <h4><strong>Add Employee</strong></h4>
                <Form className="font-all pdt-3"> 
                    <Form.Group controlId="formBasicEmail">
                    <Form.Label style={{display:"flex"}}>Email</Form.Label>
                    <div className="input-div">
                        <img className="input-img" src={require('../../../img/Email.png')} alt='email' />
                        <Form.Control type="email" name='email' className="auth-fields"  value={props.email} onChange={props.handleFields} placeholder="Enter email" />
                    </div>
                    </Form.Group>
                    <Form.Group controlId="formBasicName">
                    <Form.Label style={{display:"flex"}}>Name</Form.Label>
                    <div className="input-div">
                        <img className="input-img" src={require('../../../img/Username.png')} alt='name' />
                        <Form.Control type="text" name='name' className="auth-fields" value={props.name} onChange={props.handleFields} placeholder="Enter Name" />
                    </div>
                    </Form.Group>
                    <Form.Group controlId="formBasicNumber">
                    <Form.Label style={{display:"flex"}}>Mobile Number</Form.Label>
                    <div className="input-div">
                        <img className="input-img" src={require('../../../img/Phone.png')} alt='conatct' />
                        <Form.Control type="number" name='mobile_number' className="auth-fields" value={props.mobile_number} onChange={props.handleFields} placeholder="Enter Mobile No. " />
                    </div>
                    </Form.Group>
                    <Form.Group controlId="formBasicPassword">
                    <Form.Label style={{display:"flex"}}>Password</Form.Label>
                    <div className="input-div">
                        <img className="input-img" src={require('../../../img/Password.png')} alt='password' />
                        <Form.Control type="password" required name='password' className="auth-fields" value={props.password} onChange={props.handleFields} placeholder="Password" />
                    </div>
                    </Form.Group>
                </Form>
            </div>
            <div className="text-center mt-3 font-all">
                <button type="button" onClick={props.handleAddEmployeeSubmit} className="btn text-white btn-color-db">Submit</button>
            </div>
        </Modal.Body>
      </Modal>
    );
}

function DeleteEmployeeModal(props) {
    return (
      <Modal
        {...props}
        size="sm"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
            <div className="text-center font-all">
                <h5><strong>Are You Sure?</strong></h5>
                <h6>You want to Delete Employee.</h6>
            </div>
            <div className="text-center mt-3 font-all">
                <button type="button" onClick={()=>props.handledelete()} className="btn text-white btn-color-db">Delete</button>
                <button type="button" onClick={()=>props.onHide()} className="btn text-white btn-color-db ml-3">Cancel</button>
            </div>
        </Modal.Body>
      </Modal>
    );
}

class EmployeeListTable extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            data : [],
            pageCount:0,
            limit : 15,
            skip : 0,
            page: 0,
            addModalShow : false,
            delModalShow : false,
            emp_id: '',
            email: '',
            name:'',
            password : '',
            mobile_number : ''
        }
    }

    componentDidMount() {
        this.getEmpolyeeList();
    }

    getEmpolyeeList = async () => {
        let data1 = {
            skip : this.state.skip,
            limit : this.state.limit
        }
        let resp = await services.admin.getEmployeeList(data1);
        if(resp.success){
            this.setState({
                data : resp.data.data,
                pageCount : ~~(Math.ceil(resp.data.total/this.state.limit))
            })
        }
    }

    handleDeleteEmployee = async () => {
        let data = {
            user_id : this.state.emp_id
        }

        let _r = await services.admin.deleteEmployee(data);
        if(_r.success){
            toast.success("Deleted Successfully")
            this.setState({
                delModalShow : false,
                emp_id : ''
            },()=>this.getEmpolyeeList())
        }
    }

    setAddModalShow = (status) =>{
        this.setState({
            addModalShow : status
        })
    }

    setDeleteModalShow = (status,id) => {
        this.setState({
            delModalShow : status,
            emp_id : id
        })
    }

    handleFields = (e) => {
        this.setState({
            [e.target.name] : e.target.value
        })
    }

    handleAddEmployeeSubmit = async () => {
        let reg = /^([A-Za-z0-9_\-\.])+\@enukesoftware.com$/;

        if(this.state.email === '' || this.state.password === '' || this.state.name === '' || this.state.contact === ''){
            toast.error("All Fields Required");
        }else if (this.state.email !== '' && !reg.test(this.state.email)) {
            toast.error("Enter Correct Email")
        }else{
            let data = {
                email : this.state.email,
                name : this.state.name,
                mobile_number : this.state.mobile_number,
                password : this.state.password
            }
            let _r = await services.auth.signup(data);
            if(_r.success){
                toast.success("Successfully Added.")
                this.setState({
                    addModalShow : false,
                    email : '',
                    name: '',
                    mobile_number: '',
                    password: ''
                },()=>{
                    this.setAddModalShow(false)
                    this.getEmpolyeeList()
                })
            }else{
                toast.error("User already exist")
                this.setState({
                    email : '',
                    name: '',
                    mobile_number: '',
                    password: ''
                },()=>this.setAddModalShow(false))
            }
        }
    }

    handlePagination = (item) => {
        let selected = item.selected;
        let skip = Math.ceil(selected * this.state.limit);
        this.setState({
            skip : skip,
            page : selected+ 1
        },()=>this.getEmpolyeeList())
    }

    render(){
        console.log(this.state)
        return(
            <div className="container pdt-3 pdb-8">
                <div className="taskBox profileHead font-all mt-4">
                    <div className="table-responsive">
                    <Table className="table">
                        <thead>
                            <tr>
                                <td>
                                <h4 className="ml-3 mt-2"><b>Employee List</b></h4>
                                </td>
                                <td colspan="4" class="text-right p10 border-0">
                                <Button className="btn-color-db mt-2" onClick={() => this.setAddModalShow(true)}>
                                    Add Employee
                                </Button>
                                </td>
                            </tr>
                            <tr class="table-active ">
                                {
                                    column.map((_c)=>(
                                        <th scope="col" className="text-center">{_c.name}</th>
                                    ))
                                }
                                <th scope="col" className="text-center">Action</th>
                            </tr>
                        </thead>
                        <tbody>
                        {
                            this.state.data.length > 0 ?
                            this.state.data.map((_r,index)=>
                            <tr row={`row${index}`} className="text-center">
                                <td>{_r.name}</td>
                                <td>
                                    {
                                        _r.user_pending_task ?
                                        <button type="button" className="btn btn-sm btn-danger ml-3">{_r.user_pending_task}/{_r.user_total_task}</button>
                                        :
                                        <button type="button" className="btn btn-sm btn-success ml-3">{_r.user_pending_task}/{_r.user_total_task}</button>
                                    }
                                </td>
                                <td>{_r.email}</td>
                                <td>{_r.mobile_number}</td>
                                <td>
                                    <Link to={{pathname:"/emp_dashboard" ,state:{user_id:_r.user_id,name:_r.name}}}>
                                        
                                        <OverlayTrigger 
                                                overlay={
                                                    <Tooltip id={`tooltip-top`}>
                                                       <p className="font-all"> Click here for <strong>VIEW</strong> {_r.name}'s Task.</p>
                                                    </Tooltip>
                                                }>
                                        <i class="fa fa-eye text-success mr-3" aria-hidden="true"></i>
                                        </OverlayTrigger>
                                    </Link>
                                    <Link to={{pathname:"/emp_add_task" ,state:{user_id:_r.user_id,name:_r.name}}}>
                                        <OverlayTrigger 
                                                    overlay={
                                                        <Tooltip id={`tooltip-top`}>
                                                            <p className="font-all">Click here to <strong>ADD TASK</strong> for {_r.name}.</p>
                                                        </Tooltip>
                                                    }>
                                            <i class="fas fa-tasks text-success"></i>
                                        </OverlayTrigger>
                                    </Link>
                                    <Link to={{pathname:"/tl_member_profile" ,state:{user_id:_r.user_id,user_name:_r.name,callFrom:"EmployeeList"}}}>
                                        <OverlayTrigger 
                                                    overlay={
                                                        <Tooltip id={`tooltip-top`}>
                                                            <p className="font-all">Click here to <strong>VIEW DETAILS</strong> of {_r.name}.</p>
                                                        </Tooltip>
                                                    }>
                                            <i class="fas fa-edit ml-3 text-success"></i>
                                        </OverlayTrigger>
                                    </Link>
                                    <ToolTipComponent 
                                        message={`Click here for Delete ${_r.name} profile.`}
                                        cssClass={"ml-3"}
                                        method={()=>this.setDeleteModalShow(true,_r.user_id)}
                                        image={require('../../../img/Delete.png')}
                                    />
                                </td>
                            </tr>
                            )
                            :
                            <tr className="text-center">
                                <td colSpan="5">Sorry! There is no Employee yet.</td>
                            </tr>
                        }
                        </tbody>
                    </Table>
                    </div>
                </div>
                <div className="mt-2 pagination-center font-all">
                    <ReactPaginate
                        previousLabel={'<<'}
                        nextLabel={'>>'}
                        previousClassName={'page-item'}
                        nextClassName={'page-item'}
                        previousLinkClassName={'page-link'}
                        nextLinkClassName={'page-link'}
                        breakLabel={'...'}
                        breakClassName={'break-me'}
                        pageCount={this.state.pageCount}
                        marginPagesDisplayed={2}
                        pageRangeDisplayed={2}
                        onPageChange={this.handlePagination}
                        containerClassName={'pagination'}
                        subContainerClassName={'pages pagination'}
                        activeClassName={'active'}
                        pageClassName={'page-item'}
                        pageLinkClassName={'page-link'}
                    />
                </div>
                <DeleteEmployeeModal
                    handledelete={()=>this.handleDeleteEmployee()}
                    show={this.state.delModalShow}
                    onHide={()=>this.setDeleteModalShow(false)}
                />
                <AddEmployeeModal
                    show={this.state.addModalShow}
                    email={this.state.email}
                    name={this.state.name}
                    mobile_number={this.state.mobile_number}
                    password={this.state.password}
                    handleFields={(e)=>this.handleFields(e)}
                    handleAddEmployeeSubmit={()=>this.handleAddEmployeeSubmit()}
                    onHide={()=>this.setAddModalShow(false)}
                />
            </div>
        )
    }
}

export default EmployeeListTable;