import React from 'react';
import UpcomingTaskInnerComponent from '../HomePage/upcoming_task_inner_component'
import { Table, Button, Container, Row, Col,OverlayTrigger,Tooltip } from 'react-bootstrap';
import {withRouter} from 'react-router-dom'

class UpcomingTask extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            status : false
        }
    }

    handleAddTask = () => {
        if(window.localStorage.getItem('userType') == 1){
            this.props.history.push({
                pathname : '/emp_add_task',
                state: {
                    type : 'upcoming',
                    name: this.props.location.state.name,
                    user_id : this.props.location.state.user_id
                }
            })
        }
        if(window.localStorage.getItem('userType') == 2){
            this.props.history.push({
                pathname : '/add_task',
                state: {
                    type : 'upcoming'
                }
            })
        }
    }

    handleEditStatus = (status) => {
        if(status){
            this.setState({
                status : true
            })
        }else{
            this.setState({
                status : false
            })
        }
    }

    handleEdit = () => {
        if(window.localStorage.getItem('userType') == 1){
            this.props.history.push({
                pathname : '/emp_edit_task',
                state: {
                    type : 'upcoming',
                    name: this.props.location.state.name,
                    user_id : this.props.location.state.user_id
                }
            })
        }
        if(window.localStorage.getItem('userType') == 2){
            this.props.history.push({
                pathname : "/edit_task",
                state: {
                    type : 'upcoming'
                }
            })
        }
    }

    render(){
        return (
            <div className="container pdb-5 mt-4">
                <div className="taskBox profileHead mt-4">
                    <div className="table-responsive table-deco">
                    <table className="table table-sm">
                        <thead>
                            <tr className="font-all" >
                                <td className="border-0">
                                <h5 className="mt-2"><b>Upcoming Task's</b></h5>
                                </td>
                                <td colspan="2" className="text-right p10 border-0">
                                <Button className="btn-color-db mr-3" type="button" onClick={this.handleAddTask}>Add New Task</Button>
                                {
                                    (this.state.status ?
                                    <Button className="btn-color-green" onClick={this.handleEdit} type="button" >Edit Task</Button>
                                    :
                                    ""
                                    )
                                }
                                </td>
                            </tr>
                        </thead>
                    </table>
                    {
                        window.localStorage.getItem('userType') == 1 ?
                        <UpcomingTaskInnerComponent id={this.props.location.state.user_id} props={this.props.history} handleEditStatus={(status)=>this.handleEditStatus(status)}/>
                        :
                        <UpcomingTaskInnerComponent handleEditStatus={(status)=>this.handleEditStatus(status)}/>
                    }
                </div>
            </div>
        </div>
        )
    }
}


export default withRouter(UpcomingTask);