import React from 'react';
import { Table , OverlayTrigger , Tooltip,Modal,Button, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import services from '../../../services';
import { toast } from 'react-toastify';
import TooltipComponent from '../../common/Toottip'


function AddTeamModal(props) {
    return (
      <Modal
        {...props}
        size="sm"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
            <div className="text-center font-all">
                <h4><strong>Add Team</strong></h4>
                <input type="text" 
                    placeholder="Enter Team Name" 
                    className="mt-3 text-center" 
                    value={props.name}
                    onChange={props.handleChange}
                />
            </div>
            <div className="text-center mt-3 font-all">
                <button type="button" onClick={props.handleSubmit} className="btn text-white btn-color-db">Submit</button>
            </div>
        </Modal.Body>
      </Modal>
    );
  }
  
  function EditTeamModal(props) {
    return (
      <Modal
        {...props}
        size="sm"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
            <div className="text-center font-all">
                <h4><strong>Edit Team Name</strong></h4>
                <input type="text" 
                    placeholder="Enter Team Name" 
                    className="mt-3 text-center" 
                    value={props.name}
                    onChange={props.handleChange}
                />
            </div>
            <div className="text-center mt-3 font-all">
                <button type="button" onClick={()=>props.handleSubmit(props.id)} className="btn text-white btn-color-db">Submit</button>
            </div>
        </Modal.Body>
      </Modal>
    );
  }

  function DeleteTeamModal(props) {
    return (
      <Modal
        {...props}
        size="sm"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
            <div className="text-center font-all">
                <h5><strong>Are You Sure?</strong></h5>
                <h6>You want to Delete Team.</h6>
            </div>
            <div className="text-center mt-3 font-all">
                <button type="button" onClick={()=>props.handleDelete(props.index,props.id)} className="btn text-white btn-color-db">Delete</button>
                <button type="button" onClick={()=>props.onHide()} className="btn text-white btn-color-db ml-3">Cancel</button>
            </div>
        </Modal.Body>
      </Modal>
    );
}

class TLTeamsComponent extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            name : '',
            data : [],
            modalShow : false,
            enableEditName : '',
            team_name : '',
            delModalShow : false,
            editModalShow:false,
            team_id : '',
            index : '',
            unassigned_user : [],
            evenData : [],
            oddData : []
        }
    }

    componentDidMount() {
        this.getTeams();
    }

    getTeams = async () => {
        let _r = await services.admin.getTeams();
        if(_r.success){
            this.setState({
                data : _r.data,
                unassigned_user : _r.unassigned_user
            },()=>{
                let evenData = [...this.state.evenData];
                let oddData = [...this.state.oddData]
                for(let i=0; i<this.state.unassigned_user.length ; i++){
                    if(i%2==0){
                        evenData.push(this.state.unassigned_user[i])
                        this.setState({
                            evenData
                        })
                    }else{
                        oddData.push(this.state.unassigned_user[i])
                        this.setState({
                            oddData
                        })
                    }
                }
            })
        }
    }

    handleChange = (e) => {
        this.setState({
            name : e.target.value
        })
    }

    handleDelete = (index,id) => {
        let data = [...this.state.data];
        if (index !== -1) {
            data.splice(index, 1);
            this.setState({data},()=>this.deleteTeam(id));
        }
    }

    deleteTeam = async (id) => {
        let data = {
            team_id : id
        }
        let _r = await services.admin.deleteTeam(data);
        if(_r.success){
            toast.success("Successfully Deleted");
            this.setDeleteModalShow(false)
        }else{
            toast.error("something error")
        }
    }

    handleTeamName = (e) => {
        console.log(e.target.value)
        this.setState({
            team_name : e.target.value
        })
    }

    editTeamName = async (id) => {
        if(this.state.team_name !== ''){
            let data = {
                team_id : id,
                team_name : this.state.team_name
            }
            let _r = await services.admin.postEditTeamName(data);
            if(_r.success){
                toast.success("Updated Successfully.");
                this.setState({
                    enableEditName : ''
                },()=>{
                    this.getTeams();
                    this.setEditModalShow(false)
                })
            }else{
                toast.error("Something Wrong");
            }
        }
        else{
            toast.error("Field is empty.")
        }
    }

    setModalShow = (status) => {
        this.setState({
            modalShow : status
        })
    }

    setEditModalShow = (status,id,name) => {
        this.setState({
            editModalShow : status,
            team_id : id,
            team_name : name,
        })
    }

    setDeleteModalShow = (status,index,id) => {
        this.setState({
            delModalShow : status,
            team_id : id,
            index
        })
    }

    handleAccordian = () => {
        var acc = document.getElementsByClassName("accordion");
        var i;

        for (i = 0; i < acc.length; i++) {
        acc[i].addEventListener("click", function() {
            this.classList.toggle("active1");
            var panel = this.nextElementSibling;
            if (panel.style.maxHeight) {
            panel.style.maxHeight = null;
            } else {
            panel.style.maxHeight = panel.scrollHeight + "px";
            } 
        });
        }
    }

    handleSubmit = async () => {
        console.log("hjkjj",this.state.name)
        if(this.state.name !== ''){
            let data = {
                name : this.state.name
            }
            let _r = await services.admin.postTeamsName(data);
            if(_r.success){
                toast.success("Successfully Added")
                this.setState({
                    name : ''
                },()=>this.getTeams(),
                    this.setModalShow(false))
            }
        }else{
            toast.error("Please Fill Field")
        }
    }

    render(){
        return(
            <div class="container mt60 pdb-17 pdt-3">
                <div class="taskBox profileHead font-all mt-4">
                    <div class="table-responsive">
                    <Table class="table table-sm">
                        <thead>
                            <tr>
                                <td>
                                <h4 className="ml-3 mt-2"><b>Teams</b></h4>
                                </td>
                                <td colspan="3" class="text-right p10 border-0">
                                <Button className="btn-color-db" onClick={() => this.setModalShow(true)}>
                                    Add Team
                                </Button>
                                </td>
                            </tr>
                            <tr class="table-active text-center">
                                <th scope="col">Team</th>
                                <th scope="col">Members</th>
                                <th scope="col">Created By</th>
                                <th scope="col">Action</th>
                            </tr>
                        </thead>
                        {
                            this.state.data.length > 0 ?
                            <tbody>
                            {
                                this.state.data.map((_r,index)=>
                                <tr key={`row${index}`} className="text-center">
                                    <td>{_r.team_name}</td>
                                    <td>{_r.number_of_member}</td>
                                    <td>{_r.created_by}</td>
                                    <td>
                                        <Link to={{pathname:"/tl_team_member" , state:{id:_r.team_id,name:_r.team_name}}}>
                                            <TooltipComponent 
                                                message={`Click here for Manage Team.`}
                                                image={require('../../../img/management1.png')}
                                            />
                                        </Link>
                                        <TooltipComponent 
                                            message={`Click here for Edit Team Name.`}
                                            cssClass={"ml-3"}
                                            method={()=>this.setEditModalShow(true,_r.team_id,_r.team_name)}
                                            image={require('../../../img/Edit.png')}
                                        />
                                        <TooltipComponent 
                                            message={`Click here for Delete Team.`}
                                            cssClass={"ml-3 mb-2"}
                                            method={()=>this.setDeleteModalShow(true,index,_r.team_id)}
                                            image={require('../../../img/Delete.png')}
                                        />
                                            {/* <img className="float-right" onClick={()=>this.handleDelete(index,_r.team_id)} className="ml-3 mb-2" src={require('../../../img/Delete.png')} /> */}
                                    </td>
                                </tr>
                                )
                            }
                            </tbody>
                            :
                            <tbody>
                                <tr className="text-center">
                                    <td colSpan="4">Sorry! There is no Teams yet.</td>
                                </tr>
                            </tbody>
                        }
                    </Table>
                    </div>
                </div>
                <div className="mt-2 taskBox font-all">
                    <button className="accordion" onClick={this.handleAccordian}><b>List of Unassigned Employees</b></button>
                    <div className="panel">
                    <Row className="font-all mt-2 text-center">
                        <Col>
                            <Table class="table table-sm" bordered hover>
                                <tbody>
                                {
                                    this.state.evenData.map(user => (
                                        <tr><td>{user.name}</td></tr>
                                    ))
                                }
                                </tbody>
                            </Table>
                        </Col>
                        <Col>
                            <Table class="table table-sm" bordered hover>
                                <tbody>
                                {
                                    this.state.oddData.map(user => (
                                        <tr><td>{user.name}</td></tr>
                                    ))
                                }
                                </tbody>
                            </Table>
                        </Col>
                    </Row>
                    </div>
                </div>
                <AddTeamModal
                    show={this.state.modalShow}
                    name={this.state.name}
                    handleChange={(e)=>this.handleChange(e)}
                    handleSubmit={()=>this.handleSubmit()}
                    onHide={()=>this.setModalShow(false)}
                />
                <EditTeamModal
                    show={this.state.editModalShow}
                    name={this.state.name}
                    handleChange={(e)=>this.handleTeamName(e)}
                    handleSubmit={(id)=>this.editTeamName(id)}
                    id={this.state.team_id}
                    name={this.state.team_name}
                    onHide={()=>this.setEditModalShow(false)}
                />
                <DeleteTeamModal
                    show={this.state.delModalShow}
                    handleDelete={(index,id)=>this.handleDelete(index,id)}
                    id={this.state.team_id}
                    index={this.state.index}
                    onHide={()=>this.setDeleteModalShow(false)}
                />
            </div>
        )
    }
}


export default TLTeamsComponent;