import axios from 'axios';

axios.defaults.baseURL = 'http://worklogapi.yiipro.com/api/';
// axios.defaults.baseURL = 'http://8c95e2a8.ngrok.io/api'

const getHeaders = () => {
    const authenticationToken = window.localStorage.getItem('reset_token');
    const headers = {
      'Content-Type': 'application/json',
      Authorization: "Bearer " + authenticationToken
    };
    return headers;
};

const verifyAccountHeaders = () => {
    const authenticationToken = window.localStorage.getItem('account_verify_token');
    const headers = {
        'Content-Type': 'application/json',
        Authorization: "Bearer " + authenticationToken
    };
    return headers;
};

const appHeader = () => {
    const authenticationToken = window.localStorage.getItem('token');
    const headers = {
      'Content-Type': 'application/json',
      Authorization: "Bearer " + authenticationToken
    };
    return headers;
}

const auth = {
    login: async (email, password) => {
        try {
            const response = await axios.post('/login', {
                email,
                password
            });
            return response.data
        } catch (error) {
            let responseError = error.response ? error.response.data : { success: false}
            return responseError
        }
    },
    signup: async(data) => {
        try {
            const response = await axios.post('/register', data);
            return response.data
        } catch (error) {
            let responseError = error.response ? error.response.data : { success: false}
            return responseError
        }
    },
    resetEmailVerify: async(email) =>{
        try {
            const response = await axios.post('/forgot-password', email);
            return response.data
        } catch (error) {
            let responseError = error.response ? error.response.data : { success: false}
            return responseError
        }
    },
    resetPassword: async(password) => {
        try {
            const response = await axios.post('/reset-password', password, {
                headers: getHeaders()
            });
            return response.data
        } catch (error) {
            let responseError = error.response ? error.response.data : { success: false}
            return responseError
        }
    },
    accountVerification: async() => {
        try {
            const response = await axios.post('/email-verify',{}, {
                headers: verifyAccountHeaders()
            });
            return response.data
        } catch (error) {
            let responseError = error.response ? error.response.data : { success: false}
            return responseError
        }
    }
}

