import React from 'react';
import { Table , OverlayTrigger , Tooltip , Modal,Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import services from '../../../services';
import { toast } from 'react-toastify';
import {Typeahead} from 'react-bootstrap-typeahead';
import 'react-bootstrap-typeahead/css/Typeahead.css';
import {withRouter} from 'react-router-dom'
import ToolTipComponent from '../../common/Toottip';



function AddTeamMemberModal(props) {
    return (
      <Modal
        {...props}
        size="sm"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
            <div className="text-center font-all">
                <h5 className="mb-3"><strong>Add Team Member(s)</strong></h5>
                <Typeahead
                    clearButton
                    id="selections-example"
                    labelKey="name"
                    multiple
                    options={props.users}
                    placeholder="Select Employee"
                    selected={props.name}
                    onChange={props.handleChange}
                />
            </div>
            <div className="text-center font-all mt-3">
                <button type="button" onClick={props.handleSubmit} className="btn text-white btn-color-db">Submit</button>
            </div>
        </Modal.Body>
      </Modal>
    );
  }

  function DeleteTeamModal(props) {
    return (
      <Modal
        {...props}
        size="sm"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Body>
            <div className="text-center font-all">
                <h5><strong>Are You Sure?</strong></h5>
                <h6>You want to Delete this Member.</h6>
            </div>
            <div className="text-center mt-3 font-all">
                <button type="button" onClick={()=>props.handleDelete(props.id)} className="btn text-white btn-color-db">Delete</button>
                <button type="button" onClick={()=>props.onHide()} className="btn text-white btn-color-db ml-3">Cancel</button>
            </div>
        </Modal.Body>
      </Modal>
    );
}

class TLTeamsMemberComponent extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            name : [],
            data : [],
            users : [],
            modalShow : false,
            delModalShow : false,
            team_member_id : ''
        }
    }

    componentDidMount() {
        this.getMemberName();
    }
    
    getMemberName = async () => {
        let id = this.props.id ? this.props.id : this.props.location.state.id
        let data = {
            team_id : id
        }
        let _r = await services.admin.getMemberName(data);
        if(_r.success){
            this.setState({
                data : _r.data,
                users : _r.user
            })
        }
    }

    handleChange = (selected) => {
        // debugger;
        this.setState({ name : selected })
    }

    setModalShow = (status) => {
        this.setState({
            modalShow : status
        })
    }

    handleBack = () => {
        this.props.history.push({
            pathname : '/tl_team_management'
        })
    }

    setDeleteModalShow = (status,id) => {
        this.setState({
            delModalShow : status,
            team_member_id : id
        })
    }

    deleteTeamMember = async (id) => {
        let data = {
            team_member_id : id
        }
        let _r = await services.admin.deleteTeamMember(data);
        if(_r.success){
            toast.success("Successfully Deleted");
            this.getMemberName();
            this.setDeleteModalShow(false);
        }else{
            toast.error("something error")
        }
    }

    handleSubmit = async () => {
        console.log("hjkjj",this.state.name)
        if(this.state.name && this.state.name.length > 0){
            let id = this.state.name.map((_n)=>{
                return(_n.id)
            })
            let data = {
                team_id : this.props.id,
                user_id : id
            }
            let _r = await services.admin.postMemberName(data);
            if(_r.success){
                toast.success("Successfully Added.");
                this.setState({
                    name : []
                },()=>this.getMemberName(),
                       this.setModalShow(false) )
            }
        }else{
            toast.error("Please fill field.")
        }
    }

    render(){
        console.log(this.props.location.state)
        return(
            <div className="container text-center mt60 pdb-17 pdt-3">
                <div className="taskBox profileHead font-all mt-4">
                    <div className="table-responsive">
                    <Table className="table">
                        <thead>
                            <tr>
                                <td>
                                <h4 className="ml-3 float-left mt-2"><b>{this.props.name} Team Members</b></h4>
                                </td>
                                <td colspan="2" className="text-right p10 border-0">
                                <Button className="btn-color-db" onClick={() => this.setModalShow(true)}>
                                    Add Team Member
                                </Button>
                                </td>
                            </tr>
                            <tr class="table-active ">
                                <th scope="col">Members</th>
                                <th scope="col">Action</th>
                            </tr>
                        </thead>
                        {
                            this.state.data.length > 0 ?
                            <tbody>
                            {
                                this.state.data.map((_r,index)=>
                                <tr key={`row${index}`}>
                                    <td>{_r.user_name}</td>
                                    <td>
                                        <Link to={{pathname:"/tl_member_profile",state:{id:this.props.id,name:this.props.name,user_id:_r.user_id,user_name:_r.user_name}}}>
                                            <ToolTipComponent 
                                            message={`Click here for show Profile of Team Member.`}
                                            image={require('../../../img/Edit.png')}
                                        />
                                        </Link>
                                        <ToolTipComponent 
                                            message={`Click here for Delete Team Member.`}
                                            cssClass={"ml-3"}
                                            method={()=>this.setDeleteModalShow(true,_r.id)}
                                            image={require('../../../img/Delete.png')}
                                        />
                                    </td>
                                </tr>
                                )
                            }
                            </tbody>
                            :
                            <tbody>
                                <tr>
                                    <td colSpan="2">Sorry! No Members yet.</td>
                                </tr>
                            </tbody>
                        }
                    </Table>
                    </div>
                </div>
                <Button onClick={()=>this.handleBack()} className="btn-color-db">Back</Button>
                <AddTeamMemberModal
                    show={this.state.modalShow}
                    name={this.state.name}
                    users={this.state.users}
                    onHide={()=>this.setModalShow(false)}
                    handleChange={(e)=>this.handleChange(e)}
                    handleSubmit={()=>this.handleSubmit()}
                />
                <DeleteTeamModal
                    show={this.state.delModalShow}
                    handleDelete={(id)=>this.deleteTeamMember(id)}
                    id={this.state.team_member_id}
                    onHide={()=>this.setDeleteModalShow(false)}
                />
            </div>
        )
    }
}

export default withRouter(TLTeamsMemberComponent);