import React from 'react';
import {OverlayTrigger,Tooltip} from 'react-bootstrap';

class ToolTipComponent extends React.Component{
    constructor(props){
        super(props);

    }

    render(){
        return(
            <OverlayTrigger 
                overlay={
                    <Tooltip id={`tooltip-top`}>
                        <p className="font-all">{this.props.message}</p>
                    </Tooltip>
                }>
                <img className={this.props.cssClass} onClick={this.props.method} src={this.props.image} />
            </OverlayTrigger>
        )
    }
}

export default ToolTipComponent;