import React from 'react';
import {withRouter} from 'react-router-dom'
import {OverlayTrigger,Tooltip, Button, Alert} from 'react-bootstrap'
import services from '../../../services'
import { toast } from 'react-toastify';


class MemberProfile extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            name : '',
            email : '',
            mobile_number : 0,
            skills :'',
            password : '',
            resume : null,
            isResume : false
        }
    }

    componentDidMount() {
        this.getMemberData();
    }

    componentDidUpdate(prevProps) {
        if(window.localStorage.getItem('userType')==1){
            console.log(prevProps.id,this.props.id)
            if(prevProps.id != this.props.id){
                this.getMemberData();
            }
        }
    }

    getMemberData = async () => {
        let _r = await services.admin.getMemberData(this.props.location.state.user_id);
        if(_r.success){
            this.setState({
                name : _r.data.user_name,
                email : _r.data.email,
                mobile_number : _r.data.mobile_number,
                skills : _r.data.skills ? _r.data.skills : '',
                resume:_r.data.resume,
                isResume : _r.data.resume ? true : false
            })
        }
    }

    handleChange = (e) => {
        if(e.target.name === 'file'){
            let file = e.target.files[0];
            this.setState({
                resume : file,
                isResume : false
            })
        }else{
            this.setState({
                [e.target.name] : e.target.value
            })
        }
    }

    handleBack = () => {
        if(this.props.location.state.callFrom === 'EmployeeList'){
            console.log("hjbhjb")
            this.props.history.push({
                pathname : '/employee_list'
            })
        }else if(this.props.location.state.callFrom === 'Navbar'){
            this.props.history.push({
                pathname : '/tl_dashboard'
            })
        }else{
            this.props.history.push({
                pathname : '/tl_team_member',
                state : {
                    id : this.props.location.state.id,
                    name : this.props.location.state.name
                }
            })
        }
    }

    handleDownloadDoc = (type) => {
        if(this.state.resume){
            if(type === 'docx'){
                var filename=this.state.resume.split('/');
                let  name=filename[filename.length-1];
                let a = document.createElement('a')
                a.href = this.state.resume;
                a.download = name;
                a.click();
            }
        }else{
            toast.error("Sorry! There is no Document.")
        }
    }

    handleResumeTemplate = () => {
            let a = document.createElement('a');
            a.href = 'http://worklogapi.yiipro.com/resume/resume_template.doc'
            a.download = 'resume_template.doc';
            a.click();
    }
    
    getPDF = async () => {

            let _r = await services.admin.getPDFfromDOC(this.props.location.state.user_id);

            if(_r.success && _r.Pdf_file){
                var filename=_r.Pdf_file.split('/');
                let  name=filename[filename.length-1];
                let a = document.createElement('a');
                a.href = _r.Pdf_file
                a.target = '_blank';
                a.download = name;
                a.click();
            }else{
                toast.error("something wrong.")
            }
    }

    handleRefresh = (e) => {
            window.location.reload();
    }

    handleSubmit = async () => {
        let formData = new FormData();

        Object.keys(this.state).map((_k)=>{
            formData.append(_k+'',this.state[_k]);
        })        
        
        formData.delete('isResume')

        console.log(".........",formData)

        let id = this.props.location.state.user_id
        let _r = await services.admin.updateMemberData(formData,id)
        if(_r.success){
            toast.success("Successfully Submit.")
            this.setState({isResume : true},()=>this.getMemberData())
        }else{
            toast.error("SomeThing Error.")
        }
    }

    render(){
        console.log('ooooooooooooo',this.props.location.state)
        return (
            <div class="container pdb-12 font-all mt60">
                <div class="profileHead table-deco">
                <div class="heading">
                    <h3>{
                            this.props.name ? (`${this.props.name}'s`) + ' Profile' : 'Edit Profile' 
                        }
                    <button type="button" class="btn btn-secondary float-right" onClick={()=>this.handleResumeTemplate()}><i class="fas fa-cloud-download-alt"></i> Resume Template</button></h3>
                </div>
                <div class="prpf">

                        <div class="row align-items-center">
                            <div class="col-md-2">
                                <div class="form-group"><b>Name</b>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                <input type="text" class="form-control" onChange={this.handleChange} value={this.state.name} name="name" placeholder="Full Name" />
                                </div>
                            </div>
                            <div class="col-md-2">
                                <div class="form-group pl-2"><b>Mobile</b>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                <input class="form-control" type='number' onChange={this.handleChange} value={this.state.mobile_number || ''} name="mobile_number" placeholder="Mobile Number"/>
                                </div>
                            </div>
                        </div>
                    
                    <div class="row align-items-center">
                            <div class="col-md-2">
                                <div class="form-group"><b>Email</b>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                <input type="text" class="form-control" type='text' readOnly value={this.state.email} placeholder="Email" />
                                </div>
                            </div>
                            <div class="col-md-2">
                                <div class="form-group pl-2"><b>password</b>
                                </div>
                            </div>
                            <div class="col-md-4">
                                <div class="form-group">
                                <input class="form-control" type='password' onChange={this.handleChange} value={this.state.password} name="password" placeholder="Password" />
                                </div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-2">
                                <div class="form-group"><b>Skill</b>
                                </div>
                            </div>
                            <div class="col-md-10">
                                <div class="form-group">
                                <textarea class="form-control" onChange={this.handleChange} value={this.state.skills} name="skills" placeholder="Enter your skills" rows="3"></textarea>
                                </div>
                            </div>
                        
                        </div>
                        <div class="row">
                            <div class="col-md-2">
                                <div class="form-group"><b>Resume</b>
                                </div>
                            </div>
                            <div class="col-md-10">
                                <div class="form-group" style={{display:"flex"}}>
                                <div class="upload-btn-wrapper">
                                    <button class="btn btn-secondary"><i class="fas fa-cloud-upload-alt"></i> Upload Doc</button>
                                    <input type="file" name="file" accept=".doc" onChange={(e)=>this.handleChange(e)} />
                                </div>
                                {
                                    this.state.isResume ?
                                    <>
                                        <button type="button" class="btn btn-secondary ml-2" onClick={()=>this.handleDownloadDoc('docx')}><i class="fas fa-cloud-download-alt"></i> Download Doc</button>
                                        <button type="button" class="btn btn-secondary ml-2" onClick={()=>this.getPDF()}><i class="fas fa-file-pdf"></i> Download Pdf</button>
                                    </>
                                    :
                                    this.state.resume ?
                                    <label className="text-success ml-3 mt-2">Document Selected (Please Press "Submit" to upload the Document).</label>
                                    :
                                    <label className="text-danger ml-3 mt-2">Document not uploaded (Please Select Document and Press "Submit").</label>
                                }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="text-center mt-4">
                    <button className="btn text-white btn-color-db ml-4" onClick={this.handleSubmit} type='button'>Submit</button>
                    <button onClick={this.handleBack} className="btn btn-color-grey text-white ml-3" type='button'>Cancel</button>
                </div> 
            </div>
        )
    }
}

export default withRouter(MemberProfile);