import React from 'react';
import CommonTLNavbar from '../components/common/tl_navbar'
import DatePicker from 'react-datepicker';
import { Table,Pagination } from 'react-bootstrap';
import moment from 'moment';
import services from '../services';
import ReactPaginate from 'react-paginate';


const column = [
    {
        name:"Employee",
        key:'employee'
    },
    {
        name:"Time Spent (Hours)",
        key:'time_spent'
    },
    {
        name:"Estimated Time (Hours)",
        key:'estimated_time'
    }
]

class ReportPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            date : new Date(),
            pageCount : 0,
            activePage : 1,
            filterSelect : 'all',
            dateSelected : false,
            data : [],
            limit : 15,
            skip : 0,
            page: 0
        }
    }

    componentDidMount() {
        this.getReport();
    }

    getReport = async () => {

        let date = this.state.dateSelected ? moment(this.state.date).format('YYYY-MM-DD') : ''

        let data1 = {
            report_type : this.state.filterSelect,
            skip : this.state.skip,
            limit : this.state.limit,
            date : date
        }
        let resp = await services.admin.getReport(data1);
        // debugger;
        if(resp.success){
            console.log("klklklklk",resp)
            this.setState({
                data : resp.data.data,
                pageCount : ~~(Math.ceil(resp.data.total/this.state.limit))
            })
        }
    }

    handleFilter = (e) => {
        this.setState({
            filterSelect : e.target.value
        },()=>this.getReport())
    }

    handleChange = (e) => {
        this.setState({
            date : e
        },()=>this.getReport())
    }

    handlePagination = (item) => {
        let selected = item.selected;
        let skip = Math.ceil(selected * this.state.limit);
        this.setState({
            skip : skip,
            page : selected+ 1
        },()=>this.getReport())
    }

    handleStatus = (status) =>{
        this.setState({
            dateSelected : status
        },()=>this.getReport())
    }
    
    render(){
        console.log("fffffffffff",this.state.data)
        return(
            <div>
                <CommonTLNavbar name="Report" />
                <div className="container pdt-3 pdb-15">
                    <div className="taskBox profileHead font-all mt-4">
                        <div className="table-responsive">
                        <Table className="table">
                            <thead>
                                <tr>
                                    <td>
                                        <h4 className="ml-3 mt-2"><b>Report</b></h4>
                                    </td>
                                    <td colSpan="3" style={{textAlign:"right"}}>
                                        {
                                            this.state.dateSelected ?
                                            <div style={{textAlign:"center",display:"contents"}}>
                                                <DatePicker
                                                    className="text-center mr-2 datepicker-design"
                                                    dateFormat="dd/MM/yyyy"
                                                    selected={this.state.date}
                                                    onChange={this.handleChange}
                                                />
                                                <img onClick={()=>this.handleStatus(false)} src={require('../img/Delete.png')} />
                                            </div>
                                            :
                                            <button onClick={()=>this.handleStatus(true)} type="button" className="btn text-white btn-sm btn-color-db">Select Date 
                                                <img className="ml-2" src={require('../img/calendar.png')} />
                                            </button>
                                        }
                                        <select style={{width:"20%"}} className="ml-3 custom-select" onClick={this.handleFilter}>
                                            <option selected value="all">All</option>
                                            <option value="onLeave">On Leave</option>
                                            <option value="noTask">No Task</option>
                                            <option value="delayed">Delayed Task</option>
                                        </select>
                                    </td>
                                </tr>
                                <tr className="table-active">
                                    {
                                        column.map((_c,index)=>(
                                            <th className="text-center"><b>{_c.name}</b></th>
                                        ))
                                    }
                                    {
                                        this.state.filterSelect === 'delayed' ?
                                        <th scope='col' className="text-center"><b>Delayed Task</b></th>
                                        :
                                        <th scope='col' className="text-center"><b>Task Completed</b></th>
                                    }
                                </tr>
                            </thead>
                            <tbody>
                            {
                                this.state.data.length > 0 ?
                                this.state.data.map((_r,index)=>
                                    (
                                        _r.user.type_name === 'noTask' || this.state.filterSelect === 'noTask' ?
                                        <tr className="text-center" key={`row${index}`}>
                                            <td>{_r.user.user_name}</td>
                                            <td colSpan="3">--------- No Tasks ---------</td>
                                        </tr>
                                        :
                                        <tr className="text-center" key={`row${index}`}>
                                            <td>{_r.user.user_name}</td>
                                            {
                                                _r.user.type_name === 'onLeave' || this.state.filterSelect === 'onLeave' ?
                                                <td>On Leave</td>
                                                :
                                                <td>{_r.user.total_time_spent}</td>
                                                
                                            }
                                            <td>{_r.user.total_estimated_time}</td>
                                            {
                                                this.state.filterSelect === 'delayed' ?
                                                <td>{_r.user.total_task -_r.user.completed}</td>
                                                :
                                                <td>{_r.user.completed}/{_r.user.total_task}</td>
                                            }
                                        </tr>
                                    )
                                )
                                :
                                <tr className="text-center">
                                    <td colSpan="4">Sorry! There is Nothing.</td>
                                </tr>
                            }
                            </tbody>
                        </Table>
                        </div>
                    </div>
                    <div className="mt-2 pagination-center font-all">
                        <ReactPaginate
                            previousLabel={'<<'}
                            nextLabel={'>>'}
                            previousClassName={'page-item'}
                            nextClassName={'page-item'}
                            previousLinkClassName={'page-link'}
                            nextLinkClassName={'page-link'}
                            breakLabel={'...'}
                            breakClassName={'break-me'}
                            pageCount={this.state.pageCount}
                            marginPagesDisplayed={2}
                            pageRangeDisplayed={2}
                            onPageChange={this.handlePagination}
                            containerClassName={'pagination'}
                            subContainerClassName={'pages pagination'}
                            activeClassName={'active'}
                            pageClassName={'page-item'}
                            pageLinkClassName={'page-link'}
                        />
                    </div>
                </div>
            </div>
        )
    }
}

export default ReportPage;