import React from 'react';
import CommonNavbar from '../components/common/navbar'
import TLNavbar from '../components/common/tl_navbar'
// import TomorrowEditComingStatus from '../components/EditTask/tomorrowEditStatus'
import EditNextTask from '../components/EditTask/editNextTask'
import ColoredLine from '../components/common/horizontalLine'
import { Button } from 'react-bootstrap';
import { withRouter } from 'react-router-dom'
import services from '../services';
import DatePicker from "react-datepicker";
var moment = require('moment');


class EditTaskPage extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            statusTomorrow : true,
            dateTomorrow : '',
            data : [],
            comingTomorrow : {},
            selectedOption : 'comingYes',
            tom_date : new Date()
        }
     }

    componentDidMount() {
        if(window.localStorage.getItem('userType') == 1){
            if(this.props.location.state.type === 'today'){
                this.getEditTaskByID('today');
            }
            if(this.props.location.state.type === 'tomorrow'){
                this.getEditTaskByID('tomorrow');
            }
            if(this.props.location.state.type === 'upcoming'){
                this.getEditTaskByID('upcoming');
            }
        }
        if(window.localStorage.getItem('userType') == 2){
            if(this.props.location.state.type === 'today'){
                this.getEditTask('today');
            }
            if(this.props.location.state.type === 'tomorrow'){
                this.getEditTask('tomorrow');
            }
            if(this.props.location.state.type === 'upcoming'){
                this.getEditTask('upcoming');
            }
        }
    }

    getEditTask = async (type) => {
        console.log('eeeeeeeeeee',this.state)
        let _r = await services.task.getEditTask(type);
        
        if(_r.success){
            let date = new Date()
            this.setState({
                data : _r.data,
                comingTomorrow : _r.user_leave,
                tom_date : date,
                selectedOption : ''
            },()=>this.handleComingStatus())
        }
    }

    getEditTaskByID = async (type) => {
        let id = this.props.location.state.user_id;
        let _r = await services.admin.getEditTaskById(type,id);
        if(_r.success){
            let date = new Date()
            this.setState({
                data : _r.data,
                comingTomorrow : _r.user_leave,
                tom_date : date,
                selectedOption : ''
            },()=>this.handleComingStatus())
        }
        console.log("hfgjgjn,m",id,type,_r)
    }

    handleComingStatus = () => {
        if(this.state.selectedOption === 'comingYes'){
            this.setState({
                statusTomorrow : true,
                dateTomorrow : ''
            })
        }else if(this.state.selectedOption === 'comingNo'){
            this.setState({
                statusTomorrow : false,
                dateTomorrow : this.state.tom_date
            })
        }else{
            this.setState({
                selectedOption : ''
            })
        }
    }

    addDays = (date,jump) => {
        return(
            new Date(date.getFullYear(),date.getMonth(),(date.getDate() + jump))
        )
    }

    handleRadio = (e) => {
        this.setState({
            selectedOption : e.target.value
        },()=>this.handleComingStatus())
    }

    handleChange =(e) => {
        // console.log("Date",e)
        this.setState({
            tom_date : e
        },()=>this.handleComingStatus())
    }

    render(){
        console.log("kkkkkkkkk",this.state)
        return (
            window.localStorage.getItem("userType") == 1 ?
            <div>
                <TLNavbar name="Employee Edit Task"/>
                <div class="container mt60 pdb-5">
                    <div class="profileHead table-deco font-all">
                        <div class="heading">
                            <h4 className="mt-2"><b>Edit Task for {this.props.location.state.name}</b></h4>
                        </div>
                        <div class="prpf"></div>
                        <div className="ml-5">
                            <h3>Coming Tomorrow ?</h3>
                            <div>
                                <span class="radio-inline">
                                    <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="comingYes" checked={this.state.selectedOption === 'comingYes'} />
                                    <label className="ml-2">Yes</label>    
                                </span>
                                <span class="radio-inline">
                                    <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="comingNo" checked={this.state.selectedOption === 'comingNo'} />
                                    <label className="ml-2">No</label>
                                </span>
                            </div>
                            {
                                this.state.selectedOption === 'comingNo' && 
                                <div className="mt-3 ml-5">
                                    <b>Until :-</b>
                                    <DatePicker
                                    className="ml-2 text-center datepicker-design"
                                    dateFormat="dd/MM/yyyy"
                                    selected={this.state.tom_date}
                                    onChange={this.handleChange}
                                    minDate={this.addDays(new Date(),1)}
                                    />
                                </div>
                            }
                        </div>
                        <div className="mt-5">
                            <ColoredLine color="#000" />
                        </div>
                        <div className="mt-4">
                            <EditNextTask selectedOption={this.state.selectedOption} comingTom={this.state.statusTomorrow} comingTomDate={this.state.dateTomorrow} props={this.props} data={this.state.data}/>
                        </div>
                    </div>
                </div>
            </div>
            :
            <div>
                <CommonNavbar name="Edit Task"/>
                <div class="container mt60 pdb-5">
                    <div class="profileHead table-deco font-all">
                        <div class="heading">
                            <h4 className="mt-2"><b>Edit Task</b></h4>
                        </div>
                        <div class="prpf"></div>
                        <div className="ml-5 mt-4">
                            <h3>Coming Tomorrow ?</h3>
                            <div>
                                <span class="radio-inline">
                                    <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="comingYes" checked={this.state.selectedOption === 'comingYes'} />
                                    <label className="ml-2">Yes</label>    
                                </span>
                                <span class="radio-inline">
                                    <input style={{width:20,height:20}} onChange={this.handleRadio} className="mt-3 ml-5" type="radio" value="comingNo" checked={this.state.selectedOption === 'comingNo'} />
                                    <label className="ml-2">No</label>
                                </span>
                            </div>
                            {
                                this.state.selectedOption === 'comingNo' && 
                                <div className="mt-3 ml-5">
                                    <b>Until :-</b>
                                    <DatePicker
                                    className="ml-2 text-center datepicker-design"
                                    dateFormat="dd/MM/yyyy"
                                    selected={this.state.tom_date}
                                    onChange={this.handleChange}
                                    minDate={this.addDays(new Date(),1)}
                                    />
                                </div>
                            }
                        </div>
                        <div className="mt-5">
                            <ColoredLine color="#000" />
                        </div>
                        <div className="mt-4">
                            <EditNextTask selectedOption={this.state.selectedOption} comingTom={this.state.statusTomorrow} comingTomDate={this.state.dateTomorrow} props={this.props} data={this.state.data}/>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(EditTaskPage);